body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
html{
  background-color: rgb(228, 228, 248);
}
.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  width: 100%;
  /* background-color: rgb(208, 208, 247); */
  background-color: rgb(228, 228, 248);
  box-shadow: 0 0 10px rgb(131, 131, 146);
  margin-bottom: 20px;
  padding: 5px;
  box-sizing: border-box;
}
.circleButton{
  height: 30px;
  width: 30px;
  border-radius: 50%;
  background-color: rgb(164, 164, 209);
  position: relative;  
}
.circleButtonInline{
  position: absolute;
  left: 5px;
  top: 5px;
}
.floatingMenuContainer{
  height: 140px;
  width: 140px;
  top: 0;
  left: 0;
  position: absolute;
  padding-top: 30px;
  box-sizing: border-box;
  visibility: collapse;
}
.floatingMenuRight{  
  left: -110px;
  z-index: 2;
}
.floatingMenu{
  border-radius: 5px;
  background-color: rgb(228, 228, 248);
  box-shadow: 0 0 10px rgb(131, 131, 146);
  height: 100%;
  width: 100%;
  overflow:hidden;  
  z-index: 2;
}
.sideMenu{
  height: 800px;
  width: 200px;
  padding-top: 70px;  
  left:-180px;
  transition: 500ms;  
  visibility: visible;
  z-index: 2;
}
.sideMenu:hover{
  left: 0;  
}
.circleButton:hover .floatingMenuContainer{
  visibility: visible;
}
.setNameTitle{
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid gray;
  font-weight: bold;
}
.setNameDiv{
  cursor: pointer;
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid gray;
  position: relative;
}
.setNameDiv:hover{
  background-color: rgb(245, 245, 252);
}
.noteLineContainer{
  height: 600px;
  overflow-y: scroll;  
}
.noteLineDiv{
  cursor: pointer;
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid gray;
  position: relative;  
}
.noteLineDiv:hover{
  background-color: rgb(245, 245, 252);
}
.speechButtonsDivOuter{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-bottom: 20px;
}
.speechButtonsDiv{
  height: 80px;
  width: 450px;
  border-radius: 10px;
  background-color: rgb(228, 228, 248);
  box-shadow: 0 0 10px rgb(131, 131, 146);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
}
.button{
  height: 50px;
  width: 120px;
  border-radius: 5px;
  background-image: linear-gradient(rgb(119, 119, 247), rgb(8, 8, 48)) ;
  color: white;
}
.button:hover{
  background-image: linear-gradient(rgb(168, 168, 224), rgb(21, 21, 100)) ;
}
.noteEdit{
  position: absolute;
  background-color: rgb(228, 228, 248);
  box-shadow: 0 0 20px rgb(89, 89, 95);
  border-radius: 10px;
  height: 600px;
  width: 85%;
  margin-left: 7.5%;
  z-index: 2;
  padding: 20px;
  
  box-sizing: border-box;  
  top: 140px;  
}
.noteEdit textarea{
  overflow-y: scroll;
  height: 500px;
  width: 95%;
  outline: none;
  border: none;
  background-color: rgb(236, 236, 243);
  
}
.buttonContainer{
  display: flex;
  justify-content: space-between;
  width: 80%;
  margin-left: 10%;
  box-sizing: border-box;
  padding: 5px;
}
.closeButton{
  height: 25px;
  width: 25px;
  color: white;
  background-color: rgb(245, 148, 148);
  font-weight: bold;  
  position: absolute;
  top: 10px;
  right: 10px;
  outline: none;
  border:none;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  cursor: pointer;
}
.hidden{
  visibility:hidden
}